import ReactTable from 'react-table-v6'
import 'react-table-v6/react-table.css'
import './CustomTable.scss'
import React from 'react'

import withFixedColumns from "react-table-hoc-fixed-columns";
import "react-table-hoc-fixed-columns/lib/styles.css";
const ReactTableFixedColumns = withFixedColumns(ReactTable);

class CustomTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showModal: false,
            activeTab: '1',
            editColumnTemp: {},
        }
    }

    getColumnWidth = (rows, accessor, headerText) => {
        const cellLength = Math.max(
            ...rows.map(row => {
                let value = '';
                value = accessor(row);
                // if (typeof accessor === 'string') {
                //     value = _.get(row, accessor);
                // } else {
                //     value = accessor(row);
                // }
                if (typeof value === 'number') return value.toString().length;
                return (value || '').length;
            }),
            headerText.length
        );
        return cellLength * 12
    }

    headerIcon = (data, header, editColumn) => {
        let listHeader = []
        header && header.map((h, index) => {
            if (!editColumn[index]) {
                let withIcon = (<span>
                    {h.Header} <i className="iconU-sort"> </i>
                </span>
                )
                let obj = {
                    Header: withIcon,
                    Cell: h.Cell,
                    accessor: h.accessor,
                    sortable: h.sortable === false ? false : true,
                    resizable: h.resizable || false,
                    className: h.className || null,
                    headerClassName: h.headerClassName || null,
                    style: h.style || null,
                    width: h.width || null,
                }
                return listHeader = [...listHeader, obj]
            } else {
                return listHeader = [...listHeader]
            }
        })

        if (this.props.editColumn !== 'false') {
            let editBtn = (
                <i className='iconU-tableEdit pencilTable' />
            )
            let obj = {
                Header: editBtn,
                accessor: 'editBtn',
                width: 50,
                style: { textAlign: 'center' },
            };
            listHeader = [...listHeader, obj];
        }

        return listHeader;
    };

    render() {
        const { showModal, editColumnTemp, fields } = this.state
        let { columns, data, onClick, styles, tableStatus, className } = this.props
        let headerIcon = this.headerIcon(data, columns, editColumnTemp);
        return (

            <React.Fragment>
                <ReactTableFixedColumns
                    className={className}
                    columns={headerIcon}
                    data={data}
                    showPagination={false}
                    style={styles}
                    noDataText={tableStatus == "noData" ? 'Please Wait' : 'No  Data Found'}
                    minRows='0'
                    resizable={false}
                    getTdProps={(state, rowInfo, column, instance) => {
                        return {
                            onClick: (e, handleOriginal) => {
                                !!onClick &&
                                    onClick(rowInfo.original, state, column, e, instance);
                            },
                            style: {
                                height: "3rem",
                                cursor: !!onClick && 'pointer',
                                textAlign: isNaN(rowInfo?.original[column.id])
                                    ? 'left'
                                    : 'right',
                            },
                        };
                    }}
                    defaultPageSize={50}
                />
            </React.Fragment>
        )
    }

}

export default CustomTable