import React, { useEffect, useState } from "react";
import {
    CButton,
    CCol,
    CModal,
    CModalBody,
    CRow
} from "@coreui/react";
import "Component/Modal/PopUp/index.scss";
import { dateFull } from 'services'
import { submitAction, message, handleFailed } from './services'

const Saved = ({ submit, userName }) => {
    const [show, setShow] = useState(true)
    const [success, setSuccess] = useState()
    const saved = {
        success: {
            headerIcon: 'em em-tada',
            textColor: 'green',
            button: 'bg-green text-white',
            date: dateFull({ date: new Date }),
            title: 'Success',
            message1: 'Active Stock already synchronize on all modules',
            message2: '',
            message3: '',
            buttonText: 'Got it!',
            userName: userName ? userName : ''
        },
        failed: {
            headerIcon: 'em em-anguished',
            textColor: 'red',
            button: 'bg-red text-white',
            date: '',
            title: 'Failed',
            message1: '',
            message2: 'Please check the detail from the Active history table',
            message3: 'Active Stock failed to synchronize.',
            buttonText: 'Check history',
            userName: ''
        }
    }
    window.onkeydown = e => e.keyCode === 27 ? setShow(false) : null
    useEffect(_ => {
        const isError = submit?.data?.isSuccess
        if (submit?.data?.isSuccess) setShow(true)
        if (!submit?.data?.isSuccess) setShow(false)
        // if (isError) setSuccess(true)
        // if (!isError) setSuccess(false)
    }, [submit])
    const isSuccess = success ? saved.success : saved.failed
    return (
        <CRow className={show ? null : 'd-none'}>
            <CCol>
                <CModal
                    className={``}
                    show={show}

                    onClose={() => null}
                    size="lg"
                    style={{ minHeight: "280px" }}
                    bodyOpenClassName="p-3"
                    closeOnBackdrop={false}
                >
                    <div className="d-flex flex-row-reverse">
                        <i className="pr-3 iconU-modalClose" ></i>
                    </div>
                    <CModalBody className="px-5 py-5 d-flex justify-content-between flex-column">
                        <div>
                            <div>
                                <label className={`${isSuccess.textColor} LatoBold mr-2 h5 mb-3`}>{isSuccess.title}</label> <span><i className={`${isSuccess.headerIcon}`}></i></span>
                            </div>
                            <div className='text mb-3'>{isSuccess.message1}</div>
                        </div>
                        <div style={{ backgroundColor: '#F5F7FA', opacity: 1 }} className={`w-100 border rounded px-3 pt-3 pb-3 mb-3 ${success ? null : 'd-none'}`}>
                            <div className='mb-0'>
                                <div className={`mr-1 ${success ? null : 'd-none'}`}>
                                    <div style={{ wordWrap: 'break-word', opacity: 1, height: true ? false : 0 }} className={`mr-1`}>
                                        <div className={`h5 text mb-3 ${success ? null : 'd-none'}`}>{isSuccess.date}</div>
                                        <div style={{ fontSize: '1rem' }} className={`${success ? null : 'd-none'} font-green`}>{`By ${isSuccess.userName}`}</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div style={{ fontSize: '1rem' }} className={`${!success ? null : 'd-none'} text mb-3`}>{`${isSuccess.message3}`}</div>
                        <div style={{ fontSize: '1rem' }} className={`${!success ? null : 'd-none'} text mb-3`}>{`${isSuccess.message2}`}</div>
                        <div className='w-100 d-flex '>
                            <CButton onClick={() => setShow(false)} className={`no-shadow px-5 mr-4 mb-3 ${isSuccess.button}`}>
                                {isSuccess.buttonText}
                            </CButton>
                        </div>
                    </CModalBody>
                </CModal>
            </CCol>
        </CRow >
    );
};

export default Saved;
